// 알림창 UI 디자인
// @extend
// @mixin @include

@import "./reset.scss";
$메인칼라: #ff0000;

.wow-enter {
  opacity: 0;
}

.wow-enter-active {
  opacity: 1;
  transition: all 500ms;
}

.red {
  color: $메인칼라;
}

